document.addEventListener("DOMContentLoaded", function () {
	// template/src/blocks/modules/header/header.js
	const header = document.querySelector(".header");
	const menuList = header.querySelector(".header__menu .menu__list");
	const parentItem = menuList.querySelectorAll(".menu__item--parent");

	parentItem.forEach(item => {
		item.addEventListener("click", function () {
			item.querySelector(".menu__submenu").classList.toggle("menu__submenu--active");
		})
	})


});
